import React from 'react';
import PropTypes from 'prop-types';

import {
	Panel,
	PanelHeader,
	Placeholder,
	Button,
	Group,
	Cell,
	Avatar,
} from '@vkontakte/vkui';

import {
	Icon24ErrorCircleOutline, Icon16Verified,
} from '@vkontakte/icons';

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>Главная</PanelHeader>


		{fetchedUser && fetchedUser.id === 627581492 &&
			<Group>
				<Cell
					disabled
					before={fetchedUser.photo_200 ? <React.Fragment> <Avatar src={fetchedUser.photo_200}> </Avatar> </React.Fragment> : null}
					description={<Icon16Verified height={6} width={6} />}
				>
					Здравствуйте, {`${fetchedUser.first_name} ${fetchedUser.last_name}`}!
				</Cell>
			</Group>}

		{fetchedUser && fetchedUser.id !== 627581492 &&
			<Group>
				<Cell
					disabled
					before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}> </Avatar> : null}
					description={fetchedUser.id}
				>
					Здравствуйте, {`${fetchedUser.first_name} ${fetchedUser.last_name}`}!
				</Cell>
			</Group>}

		{!fetchedUser &&
			<Placeholder
				icon={<Icon24ErrorCircleOutline width={56} height={56} />}
				header="Не удалось получить данные"
				action={<Button size="m">Загрузить профиль</Button>}
			>
				Нажмите ниже на кнопку, чтобы загрузить профиль.
			</Placeholder>}

	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;